<template>
  <div id="contents" v-if="cls" >
    <div class="contents_area" :class="{'visib':cls}">
        <hr>
        <h2>what's new</h2>
        <h4>距上次更新已过去{{Math.ceil((new Date().getTime()-video[0].date)/86400000)}}天:)</h4>
        <a :href="`https://www.bilibili.com/video/${video[0].bvid}`" target="_blank">
          <div  class="productdiv">
            <span class="proleft"><img :src="`http:${video[0].pic}`"></span> 
            <span class="promiddle">{{video[0].title}}</span>
            <span lcass="proright">[{{formatDate(new Date(video[0].date))}}]</span>
          </div>
          </a>
        
       
        <h2>music</h2>
        <ul>
            <li class="productLi" v-for="(item,i) in mp3" v-bind:key="i">
                <a :href="item.href" target="_blank">
                  <div  class="productdiv">
                    <span class="proleft"><img :src="item.imgsrc"></span> 
                    <span class="promiddle">{{item.name}}</span>
                    <span lcass="proright">[{{item.date}}]</span>
                  </div>
                 </a>
            </li>
        </ul>
     
        <h2>video</h2>
        <ul>
            <li class="productLi" v-for="(item,i) in video" v-bind:key="i">
                <a :href="`https://www.bilibili.com/video/${item.bvid}`" target="_blank">
                  <div  class="productdiv">
                    <span class="proleft"><img :src="`http:${item.pic}`"></span> 
                    <span class="promiddle">{{item.title}}</span>
                    <span lcass="proright">[{{formatDate(new Date(item.date))}}]</span>
                  </div>
                 </a>
            </li>
        </ul>
        
    </div>
    <hr>
    <footer>
        Designed and developed by Tefric
    </footer>
  </div>
</template>

<script>
import bus from './bus'
import {video} from "../../public/vedio.js"
export default {
  name: 'contents',
  data (){
      return {
          cls:false,
          src:null,
          mp3:[
            {name:"SHE",date:"2020-03-04",href:"https://music.163.com/#/song?id=1428303017",imgsrc:'http://p2.music.126.net/o84BMSaYXJRj1ltkO7G_-A==/109951164769601168.jpg?param=130y130'},
            {name:"绝对黑 - 力元君 （remix01）（翻自 AOn3)",date:"2020-03-01",href:"https://music.163.com/#/song?id=1427509135",imgsrc:"http://p2.music.126.net/LqAcU3QHb7gkvNW4i7RmlA==/109951164760502671.jpg?param=130y130"},
            {name:"绝对黑 - 力元君 （remix02）（翻自 AOn3)",date:"2020-03-01",href:"https://music.163.com/#/song?id=1427510040",imgsrc:"http://p2.music.126.net/LqAcU3QHb7gkvNW4i7RmlA==/109951164760502671.jpg?param=130y130"}
            ],
            video:video,
          
      }
      
  },
  methods: {
    formatDate:function(date){
        let y = date.getFullYear();  
        let m = date.getMonth() + 1;  
        m = m < 10 ? '0' + m : m;  
        let d = date.getDate();  
        d = d < 10 ? ('0' + d) : d;  
        return y + '-' + m + '-' + d;  

    }
  },
  props: {
   
  },
  computed: {
      
  },
  mounted() {
      let _this=this;
      bus.$on('message', (e) => {
            _this.cls=e;
      })
      this.src=[require('@/assets/she.jpg'),require('@/assets/绝对黑.jpg')];
      

  }
}
</script>

